// src/components/Hero.jsx
"use client";
import React, { useEffect, useRef, useState } from "react";
import { motion } from "framer-motion";
import video from "../img/VSL Preview 2.mp4";
import SplitText from "./SplitText";

export default function Hero() {
  const videoRef = useRef(null);

  // estado de audio
  const audioEnabledRef = useRef(false); // para la lgica interna
  const [isAudioOn, setIsAudioOn] = useState(false); // para el render del botn

  // Variantes para animar botones y chips
  const groupVariants = {
    hidden: { opacity: 0, y: 24 },
    visible: (customDelay = 0) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: customDelay,
        duration: 0.5,
        ease: "easeOut",
        staggerChildren: 0.12,
      },
    }),
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 },
  };

  useEffect(() => {
    const el = videoRef.current;
    if (!el) return;

    // estado inicial: preview silencioso
    el.muted = true;
    el.volume = 0;
    el.currentTime = 0;

    const playPromise = el.play?.();
    if (playPromise && playPromise.catch) {
      playPromise.catch(() => { });
    }

    //  loop de 4s mientras el audio NO est activado
    const handlePreviewLoop = () => {
      if (!audioEnabledRef.current && el.currentTime > 4) {
        el.currentTime = 0;
      }
    };
    el.addEventListener("timeupdate", handlePreviewLoop);

    //  volumen segn visibilidad (solo cuando el audio est ON)
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.target !== el) return;

          const ratio = entry.intersectionRatio || 0;

          // si el audio est apagado, solo play/pause suave
          if (!audioEnabledRef.current) {
            if (ratio === 0) {
              el.pause?.();
            } else if (el.paused) {
              const p = el.play?.();
              if (p && p.catch) p.catch(() => { });
            }
            return;
          }

          // audio activado: fade de volumen con el scroll
          if (ratio > 0) {
            const volume = Math.min(1, Math.max(0, ratio * 1.2));
            el.volume = volume;
            el.muted = volume === 0;

            if (el.paused) {
              const p = el.play?.();
              if (p && p.catch) p.catch(() => { });
            }
          } else {
            el.volume = 0;
            el.muted = true;
            el.pause?.();
          }
        });
      },
      { threshold: [0, 0.25, 0.5, 0.75, 1] }
    );

    observer.observe(el);

    // pequea animacin de entrada
    const id = window.requestAnimationFrame(() => {
      el.classList.add("is-visible");
    });

    return () => {
      window.cancelAnimationFrame(id);
      observer.disconnect();
      el.removeEventListener("timeupdate", handlePreviewLoop);
    };
  }, []);

  //  activar audio (solo una vez). Despus el botn desaparece.
  const handleToggleAudio = () => {
    const el = videoRef.current;
    if (!el) return;

    // si ya est activado, no hacemos nada
    if (audioEnabledRef.current) return;

    audioEnabledRef.current = true;
    setIsAudioOn(true); // esto hace que el botn deje de renderizarse

    el.muted = false;
    el.volume = 1;
    el.currentTime = 0;
    const p = el.play?.();
    if (p && p.catch) p.catch(() => { });
  };

  //  doble click para fullscreen
  const handleExpandHero = () => {
    const v = videoRef.current;
    if (!v) return;
    try {
      if (v.requestFullscreen) {
        const fsPromise = v.requestFullscreen();
        if (fsPromise && fsPromise.catch) fsPromise.catch(() => { });
      } else if (v.webkitEnterFullscreen) {
        v.webkitEnterFullscreen();
      }
    } catch (e) {
      console.warn("No se pudo entrar en fullscreen", e);
    }
  };

  return (
    <section id="inicio" className="section hero">
      <div className="container hero__grid">
        {/* BLOQUE DE TEXTO */}
        <div className="hero__content">
          {/* TTULO ANIMADO */}
          <SplitText
            text="Ciruga plstica facial con precisin y criterio esttico"
            className="h1 hero__title"
            delay={80}
            duration={0.55}
            ease="power3.out"
            splitType="chars"
            from={{ opacity: 0, y: 40 }}
            to={{ opacity: 1, y: 0 }}
            threshold={0.2}
            rootMargin="-60px"
            textAlign="left"
          />

          {/* STATS ANIMADAS (REEMPLAZAN SUBTTULO) */}
          <motion.div
            className="hero__stats"
            variants={groupVariants}
            initial="hidden"
            animate="visible"
            custom={0.3}
          >
            <motion.div variants={itemVariants} className="stat-card">
              <div className="stat-number">15+</div>
              <div className="stat-label">Aos de Experiencia</div>
            </motion.div>

            <motion.div variants={itemVariants} className="stat-card">
              <div className="stat-number">98%</div>
              <div className="stat-label">Satisfaccin de Pacientes</div>
            </motion.div>

            <motion.div variants={itemVariants} className="stat-card">
              <div className="stat-number">1500+</div>
              <div className="stat-label">Cirugas Exitosas</div>
            </motion.div>
          </motion.div>

          {/* CTAS PRINCIPALES */}
          <motion.div
            className="hero__actions"
            style={{ display: "flex", flexWrap: "wrap", gap: "1rem", marginTop: "1.8rem" }}
            variants={groupVariants}
            initial="hidden"
            animate="visible"
            custom={0.5}
          >
            <motion.div variants={itemVariants}>
              <a href="#contacto" className="btn-hero-primary">
                Agendar Valoracin
              </a>
            </motion.div>

            <motion.div variants={itemVariants}>
              <a href="#/galeria" className="btn-hero-secondary">
                <span>Ver Casos de xito</span>
                <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                  <path d="M5 12h14" />
                  <path d="M12 5l7 7-7 7" />
                </svg>
              </a>
            </motion.div>
          </motion.div>

          {/* TRUST BADGES (NUEVO) */}
          <motion.div
            className="hero__trust"
            variants={groupVariants}
            initial="hidden"
            animate="visible"
            custom={0.8}
            style={{ marginTop: "2.5rem", display: "flex", gap: "1.5rem", flexWrap: "wrap" }}
          >
            <motion.div variants={itemVariants} className="trust-item">
              <div className="trust-icon">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"><path d="M12 22s8-4 8-10V5l-8-3-8 3v7c0 6 8 10 8 10z" /></svg>
              </div>
              <span>Seguridad Certificada</span>
            </motion.div>

            <motion.div variants={itemVariants} className="trust-item">
              <div className="trust-icon">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"><path d="M12 21.35l-1.45-1.32C5.4 15.36 2 12.28 2 8.5 2 5.42 4.42 3 7.5 3c1.74 0 3.41.81 4.5 2.09C13.09 3.81 14.76 3 16.5 3 19.58 3 22 5.42 22 8.5c0 3.78-3.4 6.86-8.55 11.54L12 21.35z" /></svg>
              </div>
              <span>Atencin Personalizada</span>
            </motion.div>

            <motion.div variants={itemVariants} className="trust-item">
              <div className="trust-icon">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"><path d="M22 12h-4l-3 9L9 3l-3 9H2" /></svg>
              </div>
              <span>Tecnologa Avanzada</span>
            </motion.div>
          </motion.div>
        </div>

        {/* VIDEO EXPLICATIVO */}
        <aside className="hero__media">
          <div className="hero__video-wrapper">
            <video
              loading="lazy"
              ref={videoRef}
              src={video}
              preload="metadata"
              autoPlay
              loop
              muted
              playsInline
              className="hero__video"
              onDoubleClick={handleExpandHero}
            />

            {/* Botn de volumen: SOLO se muestra mientras el sonido est apagado */}
            {!isAudioOn && (
              <button
                type="button"
                className="hero__mute-btn is-off"
                onClick={handleToggleAudio}
                aria-label="Activar sonido"
              >
                <svg
                  className="hero__mute-icon"
                  viewBox="0 0 24 24"
                  aria-hidden="true"
                >
                  {/* Parlante con la lnea de mute */}
                  <path d="M3 9v6h4l5 4V5L7 9H3z" fill="currentColor" />
                  <path
                    d="M16 8l5 8"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                  />
                  <path
                    d="M21 8l-5 8"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                  />
                </svg>
              </button>
            )}
          </div>
        </aside>
      </div>
    </section>
  );
}
