"use client";
import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";

// Importa tus imgenes aqu
import logoDark from "../img/Recurso 2.png";
import logoLight from "../img/Recurso 1.png";
import logo2Dark from "../img/Recurso 9.png";
import logo2Light from "../img/Recurso 8.png";

// Importa tus componentes UI
import HoverBorderGradient from "./ui/hover-border-gradient";

export default function Navbar() {
    const [isVisible, setIsVisible] = useState(true);
    const [isScrolled, setIsScrolled] = useState(false);
    const [theme, setTheme] = useState("dark");

    const lastScrollYRef = useRef(0);

    useEffect(() => {
        const scrollRoot = document.querySelector("[data-scroll-root]") || window;

        const getScrollY = () => {
            if (scrollRoot === window) {
                return window.scrollY || document.documentElement.scrollTop || 0;
            }
            return scrollRoot.scrollTop || 0;
        };

        const handleScroll = () => {
            const currentScrollY = getScrollY();
            const lastScrollY = lastScrollYRef.current;
            const scrollDifference = Math.abs(currentScrollY - lastScrollY);

            // Detectar si la pgina se movi del topo (para el blur/fondo)
            if (currentScrollY > 20) {
                setIsScrolled(true);
            } else {
                setIsScrolled(false);
            }

            // Detectar direccin para ocultar/mostrar navbar
            if (scrollDifference > 5) {
                if (currentScrollY > lastScrollY && currentScrollY > 100) {
                    setIsVisible(false);
                } else {
                    setIsVisible(true);
                }
                lastScrollYRef.current = currentScrollY;
            }
        };

        scrollRoot.addEventListener("scroll", handleScroll, { passive: true });
        handleScroll();

        return () => {
            scrollRoot.removeEventListener("scroll", handleScroll);
        };
    }, []);

    // Lgica del Tema
    useEffect(() => {
        const getTheme = () =>
            window.localStorage.getItem("theme") ||
            document.documentElement.getAttribute("data-theme") ||
            "dark";

        setTheme(getTheme());

        const handleThemeChange = (e) => {
            const newTheme = e?.detail?.theme || getTheme();
            setTheme(newTheme);
        };

        window.addEventListener("themeChange", handleThemeChange);
        const observer = new MutationObserver(() => setTheme(getTheme()));
        observer.observe(document.documentElement, { attributes: true, attributeFilter: ["data-theme"] });

        return () => {
            window.removeEventListener("themeChange", handleThemeChange);
            observer.disconnect();
        };
    }, []);

    const currentLogo = theme === "light" ? logoLight : logoDark;
    const currentLogo2 = theme === "light" ? logo2Light : logo2Dark;

    return (
        <header
            className={`navbar-new ${!isVisible ? "navbar-hidden" : "navbar-visible"} ${isScrolled ? "navbar-scrolled" : ""}`}
        >
            <div className="container navbar__wrap navbar__wrap--wide">
                <Link to="/inicio" className="navbar__brand" aria-label="Ir al inicio">
                    <img src={currentLogo} alt="Ecuador Esttica" className="logo" />
                    <img src={currentLogo2} alt="Logo secundario" className="logo2" />
                </Link>

                <nav className="navbar__menu">
                    <Link to="/inicio" className="navbar__link">Inicio</Link>
                    <Link to="/procedimientos" className="navbar__link">Procedimientos</Link>
                    <Link to="/galeria" className="navbar__link">Galera</Link>
                    <Link to="/consulta-ia" className="navbar__link">Consulta IA</Link>
                    <Link to="/contacto" className="navbar__link">Contacto</Link>

                    <div className="navbar__cta">
                        <HoverBorderGradient as="a" href="#contacto">
                            Agendar Cita
                        </HoverBorderGradient>
                    </div>
                </nav>
            </div>
        </header>
    );
}